import { useState } from "react"
import { Flex, Button, Drawer, Typography } from "minimum-ui"

import Layout from "../../components/Layout"
import styled from "styled-components"

const {
  ContentsLayout,
  ComponentWrapper,
  CodeWrapper,
  Title,
  Description,
  ComponentTitle,
  ComponentDescription,
} = Layout

const DrawerPage: React.FC = () => {
  const [open, setOpen] = useState(false)
  const [openLeft, setOpenLeft] = useState(false)
  const [openRight, setOpenRight] = useState(false)
  const [openTop, setOpenTop] = useState(false)
  const [openBottom, setOpenBottom] = useState(false)
  const [openFull, setOpenFull] = useState(false)
  const [open320, setOpen320] = useState(false)
  const [openDefault, setOpenDefault] = useState(false)

  return (
    <ContentsLayout>
      <Title>Drawer</Title>
      <Description>
        Drawer       
        .     Open  
           .
      </Description>
      <ComponentWrapper>
        <Flex direction="row" justify="center" gap={20}>
          <Button
            width="120px"
            variant="outline"
            onClick={() => setOpen(!open)}
          >
            {"Open Drawer"}
          </Button>
          {open && (
            <Drawer open={open} onClickOutside={() => setOpen(false)}>
              <Wrapper>
                <Typography.Title>Drawer</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
        </Flex>
      </ComponentWrapper>
      <CodeWrapper
        code={`{open && (
  <Drawer
    open={open}
    onClickOutside={() => setOpen(false)}>
    <div>hello world</div>
  </Drawer>
)}`}
      />
      <ComponentTitle>Direction</ComponentTitle>
      <ComponentDescription>
        Drawer     
      </ComponentDescription>
      <ComponentWrapper>
        <Flex direction="row" justify="center" gap={20}>
          <Button variant="outline" onClick={() => setOpenLeft(!open)}>
            {"Left"}
          </Button>
          <Button variant="outline" onClick={() => setOpenRight(!open)}>
            {"Right"}
          </Button>
          <Button variant="outline" onClick={() => setOpenTop(!open)}>
            {"Top"}
          </Button>
          <Button variant="outline" onClick={() => setOpenBottom(!open)}>
            {"Bottom"}
          </Button>
          {openLeft && (
            <Drawer
              open={openLeft}
              direction={"left"}
              onClickOutside={() => setOpenLeft(false)}
            >
              <Wrapper>
                <Typography.Title>Left</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
          {openRight && (
            <Drawer
              open={openRight}
              direction={"right"}
              onClickOutside={() => setOpenRight(false)}
            >
              <Wrapper>
                <Typography.Title>Right</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
          {openTop && (
            <Drawer
              open={openTop}
              direction={"top"}
              onClickOutside={() => setOpenTop(false)}
            >
              <Wrapper>
                <Typography.Title>Top</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
          {openBottom && (
            <Drawer
              open={openBottom}
              direction={"bottom"}
              onClickOutside={() => setOpenBottom(false)}
            >
              <Wrapper>
                <Typography.Title>Bottom</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
        </Flex>
      </ComponentWrapper>
      <CodeWrapper
        code={`{openLeft && (
  <Drawer
    open={openLeft}
    direction={"left"}
    onClickOutside={() => setOpenLeft(false)}>
    <div>Left</div>
  </Drawer>
)}`}
      />
      <ComponentTitle>Size</ComponentTitle>
      <ComponentDescription>
        Drawer   .      
          . (ex:  - , ex:  - )
      </ComponentDescription>
      <ComponentWrapper>
        <Flex direction="row" justify="center" gap={20}>
          <Button variant="outline" onClick={() => setOpenFull(!open)}>
            {"Full"}
          </Button>
          <Button variant="outline" onClick={() => setOpen320(!open)}>
            {"320"}
          </Button>
          <Button variant="outline" onClick={() => setOpenDefault(!open)}>
            {"default"}
          </Button>
          {openFull && (
            <Drawer
              open={openFull}
              size={document.querySelector("html")?.clientWidth}
              onClickOutside={() => setOpenFull(false)}
            >
              <Button variant="outline" onClick={() => setOpenFull(false)}>
                {"Close"}
              </Button>
              <Wrapper>
                <Typography.Title>Full</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
          {open320 && (
            <Drawer
              open={open320}
              size={320}
              onClickOutside={() => setOpen320(false)}
            >
              <Wrapper>
                <Typography.Title>320</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
          {openDefault && (
            <Drawer
              open={openDefault}
              onClickOutside={() => setOpenDefault(false)}
            >
              <Wrapper>
                <Typography.Title>default</Typography.Title>
              </Wrapper>
            </Drawer>
          )}
        </Flex>
      </ComponentWrapper>
      <CodeWrapper
        code={`{openFull && (
  <Drawer
    open={openFull}
    size={document.querySelector("html")?.clientWidth}
    onClickOutside={() => setOpenFull(false)}
  >
    <div>Full</div>
  </Drawer>
)}`}
      />
    </ContentsLayout>
  )
}

export default DrawerPage

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`
