import Layout from "../../components/Layout"
import { Divider, Flex } from "minimum-ui"

const {
  ContentsLayout,
  ComponentWrapper,
  CodeWrapper,
  Title,
  Description,
  ComponentTitle,
} = Layout

const DividerPage: React.FC = () => {
  return (
    <ContentsLayout>
      <Title>Divider</Title>
      <Description>
        Divider        .
             .
      </Description>
      <ComponentWrapper>
        <Flex>Section1</Flex>
        <Divider />
        <Flex>Section2</Flex>
      </ComponentWrapper>
      <CodeWrapper code={`<Divider />`} />
      <ComponentTitle>Size</ComponentTitle>
      <Description>Divider  .</Description>
      <ComponentWrapper>
        <Flex>Section1</Flex>
        <Divider size={5} />
        <Flex>Section2</Flex>
        <Divider size={10} />
        <Flex>Section3</Flex>
        <Divider size={15} />
        <Flex>Section4</Flex>
      </ComponentWrapper>
      <CodeWrapper
        code={`<Divider size={5} />
<Divider size={10} />
<Divider size={15} />`}
      />
      <ComponentWrapper>
        <Flex>Section1</Flex>
        <Divider size={5} />
        <Flex>Section2</Flex>
      </ComponentWrapper>
      <CodeWrapper code={`<Divider size={5} />`} />
      <ComponentTitle>Color</ComponentTitle>
      <Description>Divider  .</Description>
      <ComponentWrapper>
        <Flex>Section1</Flex>
        <Divider color="#aabbcc" />
        <Flex>Section2</Flex>
      </ComponentWrapper>
      <CodeWrapper code={`<Divider color="#aabbcc" />`} />
    </ContentsLayout>
  )
}

export default DividerPage
