import { useState } from "react"
import styled from "styled-components"
import {
  Button,
  Checkbox,
  Flex,
  Input,
  Radio,
  Select,
  Textarea,
  Typography,
} from "minimum-ui"

const { Title, Caption, Body } = Typography

const Form: React.FC = () => {
  const [radio, setRadio] = useState(0)
  const [check1, setCheck1] = useState(false)
  const [check2, setCheck2] = useState(false)
  const [check3, setCheck3] = useState(false)
  const [check4, setCheck4] = useState(false)
  return (
    <Wrapper>
      <FormWrapper>
        <Flex direction="column" align="flex-start" gap={10}>
          <Title></Title>
          <Caption>
                  .  
                .
          </Caption>
          <Input title="" />
          <Input title="" placeholder="test@email.com" />
          <Body></Body>
          <Select
            options={[
              { name: "", value: "" },
              { name: "", value: "home" },
              { name: "", value: "office" },
              { name: "", value: "share" },
            ]}
            onChange={() => null}
          />
          <Body></Body>
          <Radio
            title="4~6"
            checked={radio === 0}
            onChange={() => setRadio(0)}
          />
          <Radio
            title="6~8"
            checked={radio === 1}
            onChange={() => setRadio(1)}
          />
          <Radio
            title="8~10"
            checked={radio === 2}
            onChange={() => setRadio(2)}
          />
          <Radio
            title=""
            checked={radio === 3}
            onChange={() => setRadio(3)}
          />
          <Body></Body>
          <Checkbox
            checked={check1 === true}
            title="30"
            onChange={() => setCheck1((check) => !check)}
          />
          <Checkbox
            checked={check2 === true}
            title="1"
            onChange={() => setCheck2((check) => !check)}
          />
          <Checkbox
            checked={check3 === true}
            title="1 30"
            onChange={() => setCheck3((check) => !check)}
          />
          <Checkbox
            checked={check4 === true}
            title="2"
            onChange={() => setCheck4((check) => !check)}
          />
          <Body></Body>
          <Textarea />
          <Button primary></Button>
          <Flex>
            <Input title="test" />
            <Button>TEST</Button>
            <Select
              options={[
                { name: "", value: "" },
                { name: "", value: "home" },
                { name: "", value: "office" },
                { name: "", value: "share" },
              ]}
              onChange={() => null}
            />
            <Input />
          </Flex>
        </Flex>
      </FormWrapper>
    </Wrapper>
  )
}

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
`

const FormWrapper = styled.div`
  display: flex;
  width: 500px;
  height: 100%;
  padding: 20px;

  @media only screen and (max-width: 820px) {
    width: 100%;
  }
`

export default Form
